.signinPage{
    width: 100%;
    height: 100vh;
    background-image:
        linear-gradient(to right top,
            rgb(0, 0, 0),
            rgba(12, 12, 12, 0.503)), url('../../assets//images/signinImage.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
}

.signinPage .form_container{
    width: 55%;
    height: 75%;
    margin-top: 5rem;
    /* background-color: yellow; */
}

.signinPage .form_container .heading_container{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color:#fff;
}
.signinPage .form_container .heading_container h1{
    margin:0;
    margin-left: 5rem;
    filter:contrast(1.75);
}

.signinPage .form_container .input_area_container{
    width: 100%;
    height: 40%;
    display:flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
}

.signinPage .form_container .input_area_container .input_name_container{
    display: flex;
    width: 62%;
    height: 3.6rem;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
    margin-left: 4.5rem;
}
.signinPage .form_container .input_area_container .input_name_container .first_name{
    width: 42%;
    height: 3.6rem;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 1rem;
    cursor: pointer;
    transition:0.3s ease;
}
.signinPage .form_container .input_area_container .input_name_container .first_name:hover{
    box-shadow: rgba(255, 255, 255, 0.2) 0px 2px 14px;
}
.signinPage .form_container .input_area_container .input_name_container .first_name p{
    color:rgba(255,255,255,0.7);
    font-size: 0.8rem;
    font-weight: lighter;
}
.signinPage .form_container .input_area_container .input_name_container .first_name input{
    background-color: transparent;
    outline: none;
    border:none;
    margin-top: 0.4rem;
    color:#fff;
    font-weight: 600;
    font-size: 1rem;
    font-family: 'Lato';
}

.signinPage .form_container .input_area_container .input_email{
    width: 60%;
    height: 3.6rem;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-left: 1rem;
    cursor: pointer;
    transition:0.3s ease;
    margin-top: 1rem;
    margin-left: 4.5rem;
}

.signinPage .form_container .input_area_container .input_email p{
    color:rgba(255,255,255,0.7);
    font-size: 0.8rem;
    font-weight: lighter;
}
.signinPage .form_container .input_area_container .input_email input{
    background-color: transparent;
    outline: none;
    border:none;
    margin-top: 0.4rem;
    color:#fff;
    font-weight: 600;
    font-size: 1rem;
    font-family: 'Lato';
    width: 80%;
}
.signinPage .form_container .input_area_container .input_email:hover{
    box-shadow: rgba(255, 255, 255, 0.2) 0px 2px 14px;
}


.signinPage .form_container .button_container{
    width: 62%;
    height: 3.4rem;
    /* background-color: crimson; */
    margin-left: 4.3rem;
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.signinPage .form_container .button_container button{
    height: 100%;
    width: 40%;
    font-family: 'Lato';
    border:none;
    border-radius: 1.2rem;
    background-color: rgba(255,255,255,0.1);
    color:#ffffffde;
    font-weight: bold;
    font-size: 1rem;
    cursor:pointer;
    transition:0.3s ease;
    filter:contrast(1.75);
}
.signinPage .form_container .button_container button:hover{
    color:#fff;
    transform:translateY(-3%);
}
.signinPage .form_container .button_container button:active{
    transform:scale(95%);
}
